:root {
  --header-height: 60px;
  --active-bar-height: 4px;
  --color: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.nav_link:hover {
  color: var(--color);
}

.is-active {
  color: var(--color);
  position: relative;
}

.is-active:after {
  content: "";
  height: var(--active-bar-height);
  background-color: var(--color);
  border-radius: 8px;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}
